import styled from 'styled-components'

const Container = styled.div`
  width: 100%;
  margin: auto;

  @media (min-width: 700px) {
    width: 100%;
  }

  @media (min-width: 1024px) {
    width: 1024px;
  }

  @media (min-width: 1440px) {
    width: 1440px;
  }
`

export default Container



